.feed{
  margin: 30px auto;
  height: 500px;
  overflow-y: scroll;
  overflow-x: hidden;
  display:flex;
  flex-direction: column;
  align-items: center;
  width: 60vw;
  border-bottom: 1px solid gray;
}
.feed .feed-post{
  width: 100%;
  display:flex;
  align-items: center;
  justify-content: left;
  padding: 10px;
}
.feed .feed-post .post-info a label{
  cursor: pointer;
  color:green;
}
.feed .feed-post > div{
  padding: 10px 20px;
  margin: 5px;
}
.feed .feed-post canvas{
  border-radius: 50%;
  aspect-ratio: 1;
  width: 50px !important;
  height: 50px !important;
  cursor: pointer;
}
.feed .feed-post .post-info{
  display:flex;
  justify-content:left;
  align-items: center;
  gap:15px;
}
.feed .feed-post .post-info .paff-amount{
  max-width: 80px;
  overflow:hidden;
}
.feed .feed-post .post-info label{
  font-weight: 100;
  display:flex;
  align-items: center;
  justify-content: center;
}
.feed .feed-post .post-info label svg{
  cursor: pointer;
  color: green;
}
.feed .feed-post div p{
  font-size: 26px;
}