@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
a{
  text-decoration: none;
  color: black;
}
:root{
  --main-color: #7857fe;
  --color-1: #e91e63;
  --color-2: #f5ae10;
  --color-3: #09d69c;
  --bg-dark: #2b2c2f;
  --main-to-dark-color: var(--main-color);
  --dark-to-main-color: var(--bg-dark);
  --shadow-black-100: 0 5px 15px rgba(0,0,0,0.1);
  --shadow-black-300: 0 5px 15px rgba(0,0,0,0.3);
  --black-900: #000;
  --black-200: #eee;
  --black-100: #f7f7f7;
  --black-000: #fff;
  --black-alpha-100: rgba(0,0,0,0.05);
}
.App{
  overflow:hidden;
  font-family: Poppins, sans-serif;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 750px;
  height: 70vh;
  margin-top: 40px;
}
body{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.feed{
  margin: 30px auto;
  height: 500px;
  overflow-y: scroll;
  overflow-x: hidden;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  width: 60vw;
  border-bottom: 1px solid gray;
}
.feed .feed-post{
  width: 100%;
  display:-webkit-flex;
  display:flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: left;
          justify-content: left;
  padding: 10px;
}
.feed .feed-post .post-info a label{
  cursor: pointer;
  color:green;
}
.feed .feed-post > div{
  padding: 10px 20px;
  margin: 5px;
}
.feed .feed-post canvas{
  border-radius: 50%;
  aspect-ratio: 1;
  width: 50px !important;
  height: 50px !important;
  cursor: pointer;
}
.feed .feed-post .post-info{
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content:left;
          justify-content:left;
  -webkit-align-items: center;
          align-items: center;
  grid-gap:15px;
  gap:15px;
}
.feed .feed-post .post-info .paff-amount{
  max-width: 80px;
  overflow:hidden;
}
.feed .feed-post .post-info label{
  font-weight: 100;
  display:-webkit-flex;
  display:flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.feed .feed-post .post-info label svg{
  cursor: pointer;
  color: green;
}
.feed .feed-post div p{
  font-size: 26px;
}
nav{  
  display:-webkit-flex;  
  display:flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 100px;
  gap: 100px;
  padding: 20px;
  border-bottom: 1px solid black;
}
nav .profile-info a{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 10px;
  gap: 10px;
  text-decoration: none;
  color: black;
}
nav .profile-info canvas{
  height: 40px !important;
  width: 40px !important;
  border-radius: 50%;
}
nav .search-bar input{
  padding: 5px;
  cursor: pointer;
  outline: none;
  border: none;
  border-bottom: 1px solid gray;
}
nav .search-bar .suggested-addresses{
  display:-webkit-flex;
  display:flex;
  grid-gap:5px;
  gap:5px;
  overflow: hidden;
  width: 120%;
  height: 15px;
}
nav .search-bar .suggested-addresses a{
  color: blue;
  font-size: 13px;
}
.profileInfo{
  display:-webkit-flex;
  display:flex;
  margin-top: 20px;
  -webkit-justify-content: center;
          justify-content: center;
}
.profileInfo h2 canvas{
  height: 40px !important;
  width: 40px !important;
  border-radius: 50%;
}
.profileInfo h2{
  display:-webkit-flex;
  display:flex;
  -webkit-align-items: center;
          align-items: center;
  grid-gap:5px;
  gap:5px;
}
.publish{
  margin: 0 auto 30px auto;
  width: 90%;
  max-width: 1000px;
  padding-top: 30px;
}
.publish > p{
  height: 10px;
  margin: 5px;
  color: red;
}
.publishPaff{
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.publishPaff input{
  width: 80%;
  font-size:18px;
  padding: 5px;
  border: none;
  outline: none;
  background: none;  
  border-bottom: 1px solid gray;
}
.publishPaff button{
  font-size: 18px;
  padding: 15px;
  border-radius: 20%;
  border: none;
  outline: none;
  color: white;
  background-color: green;
  text-transform: uppercase;
  font-weight: 900;
  cursor: pointer;
}
